.transition-enter {
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0);
}
.transition-enter-active {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  -webkit-transition: -webkit-transform 1000ms ease;
  transition: -webkit-transform 1000ms ease;
  -o-transition: transform 1000ms ease;
  transition: transform 1000ms ease;
  transition: transform 1000ms ease, -webkit-transform 1000ms ease;
}
.transition-exit {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}
.transition-exit-active {
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0);
  -webkit-transition: -webkit-transform 1000ms ease;
  transition: -webkit-transform 1000ms ease;
  -o-transition: transform 1000ms ease;
  transition: transform 1000ms ease;
  transition: transform 1000ms ease, -webkit-transform 1000ms ease;
}
.Modal-modal-0-3, .c03{
  display: -ms-flexbox;
  display: flex;
  width:50%;
  border-radius: 2%;
  -ms-flex-align: center;
      align-items: center;
  background: rgba(0,0,0,0.1);
  -ms-flex-pack: center;
      justify-content: center;
  height: 37em;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  background-image:-webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 1)), color-stop(rgba(220, 220, 220, 0.3)), to(rgba(0, 0, 0, 1))), 
  url(./pika.png) !important;
  background-image:-webkit-linear-gradient(left, rgba(0, 0, 0, 1), rgba(220, 220, 220, 0.3), rgba(0, 0, 0, 1)), 
  url(./pika.png) !important;
  background-image:-o-linear-gradient(left, rgba(0, 0, 0, 1), rgba(220, 220, 220, 0.3), rgba(0, 0, 0, 1)), 
  url(./pika.png) !important;
  background-image:linear-gradient(to right, rgba(0, 0, 0, 1), rgba(220, 220, 220, 0.3), rgba(0, 0, 0, 1)), 
  url(./pika.png) !important;
  background-position: center !important;
  background-repeat: no-repeat !important;
}
